const _iconDefaultSize = 24
const _iconBaseSize = 24
const jsxicon = (iconName, color = null, size = _iconDefaultSize, fillHeight = true) => {
    const defaultSize = _iconDefaultSize
    let formattedSize = size
    if (typeof size === "string"){
      if (size === "xsmall"){
        formattedSize = `${(defaultSize / 3)}`
      } else if (size === "small"){
        formattedSize = `${(defaultSize / 3) * 2}`
      } else if (size === "medium") {
        formattedSize = `${defaultSize}`
      } else if (size === "large") {
        formattedSize = `${defaultSize * 1.5}`
      } else if (size === "xlarge") {
        formattedSize = `${defaultSize * 2.5}`
      }
      
    }

    let localIconName = Object.keys(iconDictionary).includes(iconName) ? iconName : 'nodata'
    const path = iconDictionary[localIconName].data
    const fillColor = color == null ? iconDictionary[localIconName].color : color
    const viewBox = `0 0 ${formattedSize * (_iconBaseSize / formattedSize) } ${formattedSize * (_iconBaseSize / formattedSize) }`
    const pathStyle = {
      'backgroundSize' : 'cover',
      'width' : '100%'
    }
    return (
      <svg
          width={formattedSize} height={formattedSize} viewBox={viewBox}
          className={(fillHeight ? "h-100" : "") + "svgicon icon-" + iconName}
          xmlns="http://www.w3.org/2000/svg" fillRule="evenodd" clipRule="evenodd">  
        <path style={pathStyle} fill={fillColor} d={path}/>
      </svg>
    )
}

export default jsxicon